.apexcharts-tooltip.apexcharts-theme-light .apexcharts-tooltip-title {
    background-color: #FFF!important;
}
.apexcharts-tooltip .apexcharts-tooltip-title {
    border-bottom: none!important;
    padding-top: 4px;
    padding-bottom: 0;
    padding-left: 10px;
    margin-bottom: 0;
}
.apexcharts-tooltip .apexcharts-tooltip-y-group {
    padding-top: 2px;
    padding-bottom: 2px;
}
.apexcharts-tooltip .apexcharts-tooltip-boxPlot > div {
    margin-top: 0;
    margin-bottom: 0;
}
.apexcharts-tooltip .apexcharts-tooltip-series-group.apexcharts-active {
    padding-bottom: 0;
}
.apexcharts-tooltip .apexcharts-tooltip-series-group .apexcharts-tooltip-marker {
    height: 4px;
    min-width: 12px;
    border-radius: 1px;
}
.apexcharts-tooltip .apexcharts-tooltip-series-group .apexcharts-tooltip-text {
    width: 100%;
}
.apexcharts-tooltip .apexcharts-tooltip-series-group .apexcharts-tooltip-text-y-value,
.apexcharts-tooltip .apexcharts-tooltip-candlestick .value,
.apexcharts-tooltip .apexcharts-tooltip-boxPlot .value {
    float: right;
    padding-left: 5px;
}
.apexcharts-tooltip.apexcharts-tooltip-right {
    margin-left: 50px;
}
.apexcharts-tooltip.apexcharts-tooltip-left {
    margin-left: -50px;
}

.coreui-chart {
    position: relative;
}
.coreui-chart .coreui-chart-lock {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    z-index: 2;
}
.coreui-chart .coreui-chart-lock .coreui-chart-message {
    width: fit-content;
    margin: 0 auto;
    text-align: center;
    top: 25%;
    position: relative;
    border-radius: 4px;
    background-color: #e0e0e05e;
    padding: 6px 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 8px;
}
.coreui-chart .coreui-chart-lock .spinner-border {
    --bs-spinner-width: 1.5rem;
    --bs-spinner-height: 1.5rem;
}